<style lang="less">
  @import './login.less';
</style>

<template>
  <div class="login" >
    <div class="login-con">
      <Card icon="log-in" title="登录wms-demo" :bordered="false">
        <div class="form-con">
          <login-form @on-success-valid="handleSubmit" ></login-form>
          <p class="login-tip">请输入用户名和密码</p>
        </div>
      </Card>
    </div>
  </div>
</template>

<script>
import loginForm from '_c/login-form'
import axios from 'axios'
import { mapActions } from 'vuex'
export default {
  components: {
    loginForm
  },
  methods: {
    // ...mapActions([
    //   'handleLogin',
    //   'getUserInfo'
    // ]),
    handleSubmit ({ userName, password }) {
      console.log(userName,password)
      var scope = this
      axios.request({
        url: scope.baseUrl + '/login',
        data: {
          userName: userName,
          pwd: password
        },
        method: 'post'
      }).then(function (res) {
        if (res.data.suc) {
          scope.$Message.info({
            content: '登录成功',
            duration: 2,
            onClose: function () {
              scope.$router.push({name: 'home'})
            }
          })
        } else {
          scope.$Message.error({
            content: res.data.msg,
            duration: 3
          })
        }
      })
        .catch(function (error) {
          scope.$Message.error({
            content: error || ''
          })
        })
    }

  }
}
</script>

